
@font-face {
  font-family: 'mm';
  src: url("data:application/x-font-ttf;charset=utf-8;base64,") format('truetype');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-mm-"], [class*=" icon-mm-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'mm' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-mm-instagram-logo:before {
  content: "\e900";
}
.icon-mm-linkedin-logo:before {
  content: "\e901";
  -webkit-text-fill-color: #2273b1;
}
.icon-mm-facebook-app-logo:before {
  content: "\e902";
  -webkit-text-fill-color: #4267b3;
}
